import { ScalePoint, ScaleBand, ScaleLinear } from "d3";
import { Context } from "../context";
import { Helpers } from "../type/helpters";

export type CategoryScale =
  | ScalePoint<string>
  | [
      ScaleBand<string>, // 
      ScaleBand<string> // 
    ];
export type ValueScale = ScaleLinear<number, number>;

export type CategoryScaleType = "Point" | "Band";

/**
 * 
 */
export abstract class Coordinate {
  /**
   * 
   * @since 2.0
   */
  public ctx: Context;

  /**
   * 
   * @since 2.0
   */
  public helpers: Helpers;

  /**
   * @contructor
   * @param ctx 
   * @param model 
   */
  public constructor(ctx: Context, helpers: Helpers) {
    this.ctx = ctx;
    this.helpers = helpers;
  }

  /**
   * 
   * @since 2.0
   */
  abstract draw(
    valueScale: ValueScale,
    categroryScale?: CategoryScale,
    categroryScaleType?: CategoryScaleType,
    invert?: boolean
  ): void;
}
